    dongmanxinxiChat1() {
      this.$nextTick(()=>{

        var dongmanxinxiChart1 = echarts.init(document.getElementById("dongmanxinxiChart1"),'caravan');
        this.$http({
            url: "dongmanxinxi/group/genre",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.funnelNum){
						break;
					}
                    xAxis.push(res[i].genre);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].genre
                    })
                }
                var option = {};
				let titleObj = this.funnel.title
				titleObj.text = ''
				
				let legendObj = {
					data: xAxis,
				}
				legendObj = Object.assign(legendObj , this.funnel.legend)
				let seriesObj = {
					name: '',
					data: pArray,
					type: 'funnel',
					left: '10%',
					top: 60,
					bottom: 60,
					width: '80%',
					minSize: '0%',
					maxSize: '100%',
				}
				seriesObj = Object.assign(seriesObj , this.funnel.series)
				const gridObj = this.funnel.grid
				let tooltipObj = {
					trigger: 'item',
					formatter: "{b} : {c}"
				}
				tooltipObj = Object.assign(tooltipObj , this.funnel.tooltip?this.funnel.tooltip:{})
				option = {
					backgroundColor: this.funnel.backgroundColor,
					color: this.funnel.color,
				    title: titleObj,
				    legend: legendObj,
				    tooltip: tooltipObj,
					grid: gridObj,
				    series: seriesObj,
				}
                // 
                dongmanxinxiChart1.setOption(option);
				

                  //
				window.addEventListener('resize', () => {
                    dongmanxinxiChart1.resize();
                },false);
            }
        });
      })
    },

